body {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 12px;
  line-height: 18px;
  color: $brown;
  min-width: 375px;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
