.container {
  width: 327px;
  margin: 0 auto;
  padding: 0 24px;
}
@media (min-width: 768px) {
  .container {
    width: 672px;
    padding: 0 48px;
  }
}
@media (min-width: 1024px) {
  .container {
    width: 928px;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1120px;
    padding: 0 80px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1244px;
    padding: 0 98px;
  }
}
@media (min-width: 1920px) {
  .container {
    width: 1660px;
    padding: 0 130px;
  }
}

body {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 12px;
  line-height: 18px;
  color: #383636;
  min-width: 375px;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  background-color: #f8f8f7;
}

.main-header__page-head {
  display: none;
}

.main-nav {
  position: relative;
  display: flex;
  background-color: #ffffff;
  height: 40px;
  padding: 12px 24px;
}

.main-nav__logo {
  display: block;
  background-image: url("../img/mobile/logo.svg");
  background-repeat: no-repeat;
  width: 43px;
  height: 40px;
}

.main-nav__nav-list {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  list-style: none;
  transform: translateY(-150%);
  transition: 0.4s;
  z-index: 3;
}
.main-nav__nav-list--open {
  transform: translateY(64px);
  transition: 0.4s;
}

.main-nav__profile-block {
  display: flex;
  width: 130px;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
}

.main-nav__notifications {
  width: 22px;
  height: 24px;
  background-image: url("../img/icons/notification.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  padding: 0;
  border: none;
  cursor: pointer;
}

.main-nav__profile-link {
  display: block;
  width: 40px;
  height: 40px;
  background-image: url("../img/mobile/profile-icon.png");
  background-repeat: no-repeat;
  cursor: pointer;
}

.main-nav__menu-button {
  width: 40px;
  height: 40px;
  background-image: url("../img/icons/menu-button.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  padding: 0;
  border: none;
  cursor: pointer;
}

.main-content {
  display: flex;
  flex-direction: column;
  background-color: #f8f8f7;
}

.all-pages {
  display: none;
}

.titles {
  background-color: #ffffff;
  border-radius: 20px;
  padding: 34px 24px 0 24px;
  order: 1;
}

.titles__title {
  font-size: 24px;
  line-height: 29px;
  margin-top: 0;
  margin-bottom: 4px;
}

.titles__my-points {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  font-weight: 600;
  color: #5b8388;
  margin: 0;
}

.info-bonus {
  font-size: 16px;
  line-height: 22px;
  text-decoration: none;
  position: relative;
  color: #a8a8a8;
  display: inline-block;
  background-color: #ffffff;
  padding: 11px 11px 11px 51px;
  border-radius: 20px;
}
.info-bonus::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 11px;
  background-image: url("../img/icons/pen.svg");
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
}

.info-bonus__bonus-number {
  color: #383636;
}

.profile__intro-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 26px;
}

.profile__description-block {
  margin-right: 42px;
}

.profile__my-name {
  font-size: 14px;
  line-height: 21px;
  color: #b6b6b3;
  margin: 0;
}

.profile__my-status {
  font-size: 20px;
  line-height: 28px;
  font-weight: 600;
  margin: 12px 0;
}

.profile__my-info {
  line-height: 15px;
  margin: 0;
  color: #b6b6b3;
}

.profile__friends {
  margin-top: 27px;
  margin-bottom: 30px;
}

.profile__friends-title {
  font-size: 14px;
  line-height: 20px;
  color: #b6b6b3;
  margin: 0;
}

.profile__all-friends {
  display: block;
  background-image: url("../img/mobile/friend-list.png");
  background-repeat: no-repeat;
  width: 141px;
  height: 48px;
  margin-top: 8px;
}

.profile__links {
  display: flex;
  flex-wrap: wrap;
  margin-top: 14px;
  margin-bottom: 23px;
}

.profile__links-title {
  font-size: 14px;
  line-height: 20px;
  flex-basis: 100%;
  color: #b6b6b3;
  margin-top: 0;
  margin-bottom: 5px;
}

.profile__link {
  position: relative;
  display: flex;
  align-items: center;
  line-height: 15px;
  text-decoration: none;
  font-weight: 600;
  color: #383636;
  padding-left: 32px;
}
.profile__link::before {
  content: "";
  position: absolute;
  left: 0;
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
}
.profile__link--vkontakte {
  flex-basis: 45%;
}
.profile__link--vkontakte::before {
  background-image: url("../img/icons/vk.svg");
}
.profile__link--facebook {
  flex-basis: 35%;
}
.profile__link--facebook::before {
  background-image: url("../img/icons/fb.svg");
}
.profile__link--discord {
  margin-top: 15px;
}
.profile__link--discord::before {
  background-image: url("../img/icons/discord.svg");
}

.main-footer {
  background-color: #383636;
}